/* Domaine Sans Web */
@font-face {
  font-family: "Domaine Sans Text";
  src: url("/assets/fonts/domaine-sans-text/DomaineSansTextWeb-Light.woff2") format("woff2"),
       url("/assets/fonts/domaine-sans-text/DomaineSansTextWeb-Light.woff2") format("woff2"),
       url("/assets/fonts/domaine-sans-text/DomaineSansTextWeb-Light.eot") format("eot");
  font-style: normal;
  font-weight: 300;
}

@font-face {
  font-family: "Domaine Sans Text";
  src: url("/assets/fonts/domaine-sans-text/DomaineSansTextWeb-LightItalic.woff2") format("woff2"),
       url("/assets/fonts/domaine-sans-text/DomaineSansTextWeb-LightItalic.woff") format("woff"),
       url("/assets/fonts/domaine-sans-text/DomaineSansTextWeb-LightItalic.eot") format("eot");
  font-style: italic;
  font-weight: 300;
}

@font-face {
  font-family: "Domaine Sans Text";
  src: url("/assets/fonts/domaine-sans-text/DomaineSansTextWeb-Medium.woff2") format("woff2"),
       url("/assets/fonts/domaine-sans-text/DomaineSansTextWeb-Medium.woff") format("woff"),
       url("/assets/fonts/domaine-sans-text/DomaineSansTextWeb-Medium.eot") format("eot");
  font-style: normal;
  font-weight: 500;
}

